import React from 'react'
import {  FaRocketchat,FaBell,} from "react-icons/fa";
import { Link } from "react-router-dom";
import OlxLogo from "../../assets/image/OlxLogo.png";
import FrontProfile from "../../assets/image/FrontProfile.png";
import { useSelector } from "react-redux";





export default function LoginComponent() {
  const photoURL = useSelector(state => state.AuthReducer.user.photoURL)

    return (
        <div className="mini">
         
           <FaRocketchat  size={37} style={{ fill: "black", position: 'relative', top:'6px', left: '20px'}}/>
         
            <FaBell  size={37} style={{ fill: "black",  position: 'relative', top:'6px', left: '30px'}}/>
            
                <img src={photoURL}   alt="" style={{fill:'black',  position: 'relative',left: '45px',top:'6px', width: '45px', height:'45px', borderRadius: '50%',}} />
        
           
            
            
        </div>
    )
}
